import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import { Routes, RouterModule, Router, NavigationExtras } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { Smart } from '../../../layouts/smart-search/smart-search.component';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./opprlist.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})
export class OpprListComponent implements OnInit,AfterViewInit {
	@ViewChild(SmartSearchComponent)smart: SmartSearchComponent;
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ListComponent) list: ListComponent;
	
	configSalesopprName = sessionStorage.getItem('configSalesopprName');
	smartHide: boolean = true;
	animateSmart: string = 'out';
	 
	opprId = 0;	
	  
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private router: Router) {
		console.log("sessionStorage.getItem('configSalesopprName')");
		console.log(sessionStorage.getItem('configSalesopprName'));
    }

    ngOnInit() {
		this.smart.smartUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/sales/opportunity-smartsearch";
		this.list.listUrl = sessionStorage.getItem('requestUrl')+'/axelacrm/sales/opportunity-list';
	}
	  
	  
	 toggleAnimation(){
		   this.router.navigate(['sales/oppr-list/Oppradd'], {queryParams : { opprId : this.opprId}});
	  }
	  
	  smartClickListener(event){
		  this.list.listParam['requestType']='filter';
		  this.list.listParam['smartParam']=event;
		 this.list.getData();
	 }
	  
	  smartToggle(){
		  if(this.smartHide){
			  this._script.load('.m-grid__item.m-grid__item--fluid.m-wrapper',
            'assets/demo/default/custom/components/forms/widgets/bootstrap-select.js');
			  this.animateSmart = 'in';
			  this.smartHide = false;
		  }else{
			  this.animateSmart='out';
			  this.smartHide = true;
		  }
	  }
	  

     ngAfterViewInit(){
	}
	
	  
	  listClickListener(listParam: any[]){
		  if(listParam[0] == 'customerList'){
			  this.router.navigate(['/customer/customerlist/'], {queryParams : { customerId : listParam[1]}} );
		  }
		  if(listParam[0] == 'contactList'){
			  this.router.navigate(['/customer/customercontactlist/'], {queryParams : { contactId : listParam[1]}} );
		  }
		if(listParam[0] == 'updateOpportunity'){
			this.opprId = listParam[1];
			this.router.navigate(['sales/oppr-list/Opprupdate'], {queryParams : { opprId : this.opprId}} );  
			
		}
		 if(listParam[0] == 'addQuote'){
			this.opprId = listParam[1];
			this.toggleAnimation();
		}
		 if(listParam[0] == 'addSalesOrder'){
			this.opprId = listParam[1];
			this.toggleAnimation();
		}
		  if(listParam[0] == 'addSalesOrder'){
			this.opprId = listParam[1];
			this.toggleAnimation();
		}
		  if(listParam[0] == 'opportunityDash'){
			this.opprId = listParam[1];
			this.router.navigate(['/sales/oppr-dash/'], {queryParams : { opprId : this.opprId}} );  
			  
			  let navigationExtras: NavigationExtras = {
				  queryParams:{
					  "opprId": this.opprId, 
					 // "contactId": this.opportunity.opprContactId,
					 // "customerId":this.opportunity.opprCustomerId
				  }
			  };
			  this.router.navigate(['/sales/oppr-dash/'], navigationExtras)
		}
	}
	  
}



